@import  "index.less";
.reset-this {
    animation : none;
    animation-delay : 0;
    animation-direction : normal;
    animation-duration : 0;
    animation-fill-mode : none;
    animation-iteration-count : 1;
    animation-name : none;
    animation-play-state : running;
    animation-timing-function : ease;
    backface-visibility : visible;
    background : 0;
    background-attachment : scroll;
    background-clip : border-box;
    background-color : transparent;
    background-image : none;
    background-origin : padding-box;
    background-position : 0 0;
    background-position-x : 0;
    background-position-y : 0;
    background-repeat : repeat;
    background-size : auto auto;
    border : 0;
    border-style : none;
    border-width : medium;
    border-color : inherit;
    border-bottom : 0;
    border-bottom-color : inherit;
    border-bottom-left-radius : 0;
    border-bottom-right-radius : 0;
    border-bottom-style : none;
    border-bottom-width : medium;
    border-collapse : separate;
    border-image : none;
    border-left : 0;

    border-left-color : inherit;
    border-left-style : none;
    border-left-width : medium;
    border-radius : 0;
    border-right : 0;
    border-right-color : inherit;
    border-right-style : none;
    border-right-width : medium;
    border-spacing : 0;
    border-top : 0;
    border-top-color : inherit;
    border-top-left-radius : 0;
    border-top-right-radius : 0;
    border-top-style : none;
    border-top-width : medium;
    bottom : auto;
    box-shadow : none;[class^="span"] { background-color: rgba(255, 0, 0, 0.3); }
    box-sizing : content-box;
    caption-side : top;
    clear : none;
    clip : auto;
    color : inherit;
    columns : auto;
    column-count : auto;
    column-fill : balance;
    column-gap : normal;
    column-rule : medium none currentColor;
    column-rule-color : currentColor;
    column-rule-style : none;
    column-rule-width : none;
    column-span : 1;
    column-width : auto;
    content : normal;
    counter-increment : none;
    counter-reset : none;
    cursor : auto;
    direction : ltr;
    display : inline;
    empty-cells : show;
    float : none;
    font : normal;
    font-family : inherit;
    font-size : medium;
    font-style : normal;[class^="span"] { background-color: rgba(255, 0, 0, 0.3); }
    font-variant : normal;
    font-weight : normal;
    height : auto;
    hyphens : none;
    left : auto;
    letter-spacing : normal;
    line-height : normal;
    list-style : none;
    list-style-image : none;
    list-style-position : outside;
    list-style-type : disc;
    margin : 0;
    margin-bottom : 0;
    margin-left : 0;
    margin-right : 0;
    margin-top : 0;
    max-height : none;
    max-width : none;
    min-height : 0;
    min-width : 0;
    opacity : 1;
    orphans : 0;
    outline : 0;
    outline-color : invert;
    outline-style : none;
    outline-width : medium;
    overflow : visible;
    overflow-x : visible;
    overflow-y : visible;
    padding : 0;
    padding-bottom : 0;
    padding-left : 0;
    padding-right : 0;
    padding-top : 0;
    page-break-after : auto;
    page-break-before : auto;
    page-break-inside : auto;
    perspective : none;
    perspective-origin : 50% 50%;
    position : static;
    /* May need to alter quotes for different locales (e.g fr) */
    quotes : '\201C' '\201D' '\2018' '\2019';
    right : auto;
    tab-size : 8;
    table-layout : auto;
    text-align : inherit;
    text-align-last : auto;
    text-decoration : none;
    text-decoration-color : inherit;
    text-decoration-line : none;
    text-decoration-style : solid;
    text-indent : 0;
    text-shadow : none;
    text-transform : none;
    top : auto;
    transform : none;
    transform-style : flat;
    transition : none;
    transition-delay : 0s;
    transition-duration : 0s;
    transition-property : none;
    transition-timing-function : ease;
    unicode-bidi : normal;
    vertical-align : baseline;
    visibility : visible;
    white-space : normal;
    widows : 0;
    width : auto;
    word-spacing : normal;
    z-index : auto;
    /* basic modern patch */
    all: initial;
    all: unset;
}
@red: #FF3939;
@black:#242424;
@white:#FFFFFF;
@gray:#E6E4DF;

@grid_width: 1110px;
body{
  // width: 1440px;
}

.advantage-font{
  font-family: Bw Modelica LGC;
  font-style: normal;
  font-weight: bold;
  font-size: 100px;
  line-height: 120px;
}
.sensive_font(@color){
  font-family: Source Sans Pro;
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 23px;
  color: @color;
}
.basic_font(@color){
  .sensive_font(@color);
    font-size: 20px;
  font-weight: normal;
}
.small-font(@color){
  .sensive_font(@color);
  font-size: 18px !important;
}
.impotant-font(@black){
  .sensive_font(@black);
  font-weight: bold;
font-size: 24px;
line-height: 30px;
}
.most-wanted-font(@color){
  font-family: Source Sans Pro;
font-style: normal;
font-weight: 900;
font-size: 64px;
line-height: 60px;
  color: @color;
}
/* basic modern patch */
img{
  // display: block;
  height: auto;
  max-width: 100%;
}
.down-title{
  margin-top: 120px;
}
#reset-this-root {
    all: initial;
    * {
        all: unset;
    }
}
.header-plus-first-block{
height: 800px;
}
.header-container{
  height: 129px;
  box-shadow: 0px 10px 39px rgba(0, 0, 0, 0.09);
}
.main-logo{
  margin-left: 80px;
  padding-top: 40px;
  padding-bottom: 40px;
}
#menu-header_menu{
  display: flex;
  list-style-type: none;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 49px;
  padding-bottom: 57px;
  text-decoration: none;
}
.menu-item{
  padding-right: 22px;
.small-font(@black);
:hover{
  text-decoration: none;
  color:@black;
}
}
li a{
  color:@black;
}
.some-phone p{
  padding-top: 52px;
  padding-bottom: 52px;
  .sensive_font(@black);
}
.call-me{
  padding-top: 38px;
  padding-bottom: 38px;
.callback{
  display: flex;
  padding: 15px 42px;
  flex-direction: row;
  flex-wrap: nowrap;
  border: 2px solid @red;
  box-sizing: border-box;
  width: 230px;
height: 53px;
justify-content: center;
align-items: center;
}
a.callback__button{
  text-decoration: none;
  text-align: center;
  .sensive_font(@red);

}a.callback__button:hover{
  color :@red;
  text-decoration: none;
}
}
